import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import DefiDevelopmentScreen from "../screens/defi-development-company/defi-development-screen";

const DefiDevelopmentPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "DeFi Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <DefiDevelopmentScreen db={db} />
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="DeFi Development Company | Justtry Technologies"
    headTitle="DeFi Development Company | DeFi Development Services"
    description="We are a leading DAO Development Service Provider in India, providing smart contract, utility/governance token creation, and platform development."
    keywords="DeFi Development Company, DeFi development services, DeFi solutions, DeFi Development, DeFi projects, DeFi Protocols, DeFi Lending and Borrowing Solutions, DeFi Wallet Development, DeFi applications, DeFi Development Process, decentralized finance (DeFi), DeFi platform development, DeFi platform, DeFi token, DeFi token creation and management, DeFi development solutions, decentralized finance platform"
  ></Seo>
);

export default DefiDevelopmentPage;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
